import React from "react";
import {
  ContactContainer,
  Form,
  ContactH1,
  TopLine,
  Button,
  TextArea,
  Input,
} from "./ContactElements";
import { db } from "../Contact/firebase.js";
import { useState } from "react";

function Contact() {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");
  const [sent, setSent] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();

    db.collection("contacts")
      .add({
        name: name,
        message: message,
        email: email,
      })
      .then(() => {
        // alert("Thanks, your message has been sent.");
        setSent(true);
      })
      .catch((error) => {
        alert(error.message);
      });
    setName("");
    setEmail("");
    setMessage("");
  };

  return (
    <ContactContainer id="contact">
      <ContactH1>Let's get in touch.</ContactH1>
      <Form className="form" onSubmit={handleSubmit}>
        <TopLine>Name</TopLine>
        <Input
          placeholder="Enter Your Name"
          value={name}
          required
          onChange={(e) => setName(e.target.value)}
        />

        <TopLine>Email</TopLine>
        <Input
          required
          placeholder="Enter Your Email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        />

        <TopLine>Message</TopLine>
        <TextArea
          required
          placeholder="Enter Your Message"
          value={message}
          onChange={(e) => setMessage(e.target.value)}
        ></TextArea>
        <TopLine>{sent && "Thanks! Your message has been sent."}</TopLine>
        <Button type="submit" primary={true} dark={true}>
          Submit
        </Button>
      </Form>
    </ContactContainer>
  );
}

export default Contact;
