import React, { useState } from "react";
import { Button } from "../ButtonElement";
import particlesBg from "../../images/bgImg2.jpg";
import Particles from "react-particles-js";
import {
  HeroContainer,
  HeroBg,
  HeroContent,
  HeroH1,
  HeroP,
  HeroBtnWrapper,
  ArrowForward,
  ArrowRight,
} from "./HeroElements";

const HeroSection = () => {
  const [hover, setHover] = useState(false);

  const onHover = () => {
    setHover(!hover);
  };

  return (
    <>
      <Particles
        params={{
          particles: {
            number: {
              value: 30,
              density: {
                enable: true,
                value_area: 900,
              },
            },
            shape: {
              type: "square",
              stroke: {
                width: 5,
                color: "#61dafb",
              },
            },
          },
        }}
      />
      <HeroContainer id="home">
        <HeroBg
          style={{
            backgroundImage: `url(${particlesBg})`,
            backgroundSize: "cover",
            backgroundPosition: "center",
          }}
        ></HeroBg>
        <HeroContent>
          <HeroH1>
            Looking for a <span style={{ color: "#61dafb" }}>React</span>{" "}
            developer?
          </HeroH1>
          <HeroP>
            Look{" "}
            <span style={{ borderBottom: "3px solid #61dafb" }}>no more</span>.
          </HeroP>
          <HeroBtnWrapper>
            <Button
              to="projects-content"
              onMouseEnter={onHover}
              onMouseLeave={onHover}
              dark="true"
              primary="true"
              smooth={true}
              duration={500}
              spy={true}
              exact="true"
              offset={-80}
            >
              See projects. {hover ? <ArrowForward /> : <ArrowRight />}
            </Button>
          </HeroBtnWrapper>
        </HeroContent>
      </HeroContainer>
    </>
  );
};

export default HeroSection;
